.App {
}

.TodoApp {
  --primary-color: #ff9900;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.TodoApp button,
.TodoApp input {
  border-radius: 0.3rem;
  padding: 0.25rem;
}

.TodoApp button {
  border-width: 0;
}

.TodoApp button:hover {
  cursor: pointer;
}

.TodoApp button[type='submit'] {
  background-color: var(--primary-color);
  margin-left: 0.3rem;
  padding: 0.35rem;
}

.TodoApp ul {
  list-style: none;
  padding-left: 0;
}

.TodoApp ul li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-bottom: 0.5rem;
}
